/* ==========================================================================
   Blog List widget style - begin
   ========================================================================== */

.widget.edgtf-blog-list-widget {
    margin: 0 0 20px;

    > h4 {
        margin: 0 0 15px;
    }

    .edgtf-post-title a {
        font-style: inherit;
        color: inherit;
        font-size: inherit;
        text-transform: inherit;
        font-family: inherit;
        font-weight: inherit;
        letter-spacing: inherit;
        line-height: inherit;
    }

    footer .edgtf-footer-top &,
    .edgtf-top-bar & {

        > h2 {
            margin: 0 0 8px;
        }
    }
}
/* ==========================================================================
   Blog List widget style - end
   ========================================================================== */