@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.edgtf-comment {
		
		&.edgtf-pingback-comment {
			
			.edgtf-comment-text {
				padding: 0;
			}
		}
	}

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            padding: 14px 0 0;

            .edgtf-comment{
                border-bottom: 1px solid $default-border-color;
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .edgtf-comments-title {
        margin-bottom: 25px;
        padding-left: 18px;
        position: relative;

        &:before{
            content: '';
            height: 100%;
            position: absolute;
            left: 0;
            background-color: rgba(32,32,32,.3);
            width: 1px;
        }

        > * {
            margin: 0;
        }
    }

    .edgtf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 70px;
        height: 70px;
        float: left;

        img {
            display: block;
        }
    }

    .edgtf-comment-text {
        padding: 13px 0 23px 99px;
        min-height: 82px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 6px 0 4px;
            text-align: right;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 16px;
            color: $first-main-color;
            font-family: $default-heading-font;

            &:hover {
                color: $default-heading-color;
            }
        }

        .comment-reply-link{
            position: relative;
            margin-right: 10px;

            &:after{
                content: url(../img/arrow_right.png);
                right: -10px;
                position: relative;
                bottom: 5px;
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            color: rgba($default-heading-color,.4);
            font-size: 16px;
            font-style: italic;
            margin: 0 0 26px;
        }

        .edgtf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                font-size: 14px;
                margin: 0 0 10px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            color: $first-main-color;
            font-size: 12px;
            margin-left: 10px;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .edgtf-comment-form-inner {
        padding: 62px 0 78px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
        padding-left: 18px;
        position: relative;

        &:before{
            content: '';
            height: 100%;
            position: absolute;
            left: 0;
            background-color: $default-border-color;
            width: 1px;
        }
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }

    .form-submit{
    	margin-top: 30px;
    }

	#submit_comment{
		width: 100%;
	    color: #000;
	    border: none;
		background-image: url("../img/default_pattern.png");
	    background-color: transparent;
	    background-repeat: repeat;

	    &:hover{
	    	background: $first-main-color;
	    }
	}
}

@include ipad-portrait {

    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .edgtf-comment-text {

            .edgtf-comment-info {
                text-align: left;
            }
	        
            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .edgtf-comment-holder {

        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
    border-bottom: 1px solid #e0e4f2;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include edgtfButtonDefaultStyle();
	@include edgtfButtonMediumParams();
	@include edgtfButtonSolidColor();
	cursor: pointer;
	
	&:hover {
        color: #fff;
        background: $first-main-color;
	}
}

.post-password-form input[type='submit'] {
    margin-top: 20px;
    border: none;
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 20px;
    height: 150px;
}

.edgtf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

#respond {
    #commentform {
        > input{
            width: 49%;
        }

        #email{
            float: right;
        }
    }
}

.edgtf-custom-form{
    input[type='text'] {
        border-bottom: 1px solid #e0e4f2;
    }

    input.wpcf7-submit {
        width: 100%;
        border: none;
        background-image: url(../../../../themes/aalto/assets/img/default_pattern.png);
        background-repeat: repeat;
        background-color: #fff;
        color: $default-heading-color;
    }
}

.edgtf-two-columns-form {
    display: table;
    width: 100%;

    .edgtf-column-inner {
        vertical-align: middle;
        display: table-cell;

        &:first-child {
            padding-right: 9px;
        }

        &:last-child {
            width: 1%;
            text-align: left;
            padding-left: 9px;
        }
    }

    &.edgtf-no-space{
        .edgtf-column-inner{
            padding: 0;
            vertical-align: top;
        }
    }
}

.edgtf-newsletter-footer {
    input,
    input[type=email] {
        border: none;
        border-bottom: 1px solid rgba(255,255,255,.2);
        background-color: transparent;
        color: rgba(255,255,255,.5);
        font-weight: 300;
        font-size: 15px;
        font-family: $default-text-font;
        padding: 11px 20px;
        padding-left: 0;

        &:focus {
            border: none;
            border-bottom: 1px solid rgba(255,255,255,.2);
            color: #fff;
        }

        @include edgtfPlaceholder {
            color: inherit;
        }
    }
    input.wpcf7-form-control.wpcf7-submit {
        font-family: linea-basic-10;
        height: 43px;
        line-height: 43px;
        font-size: 19px;
        padding: 0;
        background-color: transparent;

        &:hover {
            border-color: rgba(255,255,255,.2);
            background-color: transparent;
            background: transparent;
        }
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
	visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

	&.edgtf-slider-is-moving {

		.owl-item {

			.edgtf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}

    &.edgtf-slider-has-both-nav {

        .owl-nav {

            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include edgtfTransform(translateY($pos));
            }
        }
    }

	.owl-nav {

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-background-color;
            background-color: $default-heading-color;
			@include edgtfTransform(translateY(-50%));

			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 30px;
				line-height: 1;
                padding: 17px;

				&:before {
					display: block;
					line-height: inherit;
					@include edgtfTransform(translateX(0));
					@include edgtfTransitionTransform(.15s ease-out);
				}
			}
		}

		.owl-prev {
			left: 0;

			&:hover{
				span:before{
					@include edgtfTransform(translateX(-3px));
				}
			}
		}

		.owl-next {
			right: 0;

			&:hover{
				span:before{
					@include edgtfTransform(translateX(3px));
				}
			}
		}
	}

	.owl-dots {
		text-align: center;
		margin: 16px 0 0;

		.owl-dot {
			display: inline-block;
			vertical-align: middle;

			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: transparent;
				border: 2px solid rgba($default-heading-color, .5);
				box-sizing: border-box;
				@include edgtfTransition(background-color .2s ease-out, border .2s ease-out);
			}

			&:hover,
			&.active {

				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}

	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #Global Slick Slider - start
   ========================================================================== */

.edgtf-slick-dots {
    margin: 50px 0 30px;
    text-align: center;

    li {
        display: inline-block;
        position: relative;
        margin: 0 8px;
        padding: 2px 0;
        cursor: pointer;

        span {
            display: block;
            background: $first-main-color;
            width: 20px;
            height: 2px;
        }
    }
}

/* ==========================================================================
   #Global Slick Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.edgtf-404-page {

    &.edgtf-wrapper {
        padding: 0 !important;
    }

    .edgtf-content {
        height: 100vh;
        margin-top: 0!important;

	    .admin-bar & {
		    height: calc(100vh - 32px);
	    }
    }

    .edgtf-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .edgtf-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 20%;
	    text-align: center;

        .edgtf-404-title {
            margin: 0;
            font-size: 198px;
            line-height: 1em;
        }

	    .edgtf-404-subtitle {
            margin: 0;
        }

	    .edgtf-404-text {
		    margin: 10px 0 0;
	    }

        .edgtf-btn {
            margin: 44px 0 0;

	        &.edgtf-btn-light-style {
		        color: $default-heading-color;
		        background-color: #fff;
		        border-color: #fff;

                .edgtf-btn-lines{
                    background-color: $default-heading-color !important;
                }

		        &:hover {
			        color: #fff !important;
			        background-color: $default-heading-color !important;
		        }
	        }
        }
    }

    @include ipad-portrait {

        .edgtf-page-not-found {
            padding: 40px 30px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    width: 41px;
    right: 20px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include edgtfTransition(opacity 0.3s ease);
    }

    > span:not(.edgtf-btn-lines) {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 13px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;
        @include edgtfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
            @include edgtfTransition(.15s ease .05s);
        }

        &:hover {
            background-color: $default-heading-color;
            border: 1px solid $default-heading-color;

            i {
                @include edgtfTransform(translateY(-8px));
                @include edgtfTransition(.2s cubic-bezier(1,-1.21, 0.25, 1));
            }
        }
    }

    .edgtf-btn-lines{
        content: '';
        position: absolute;
        background-color: #999;
        z-index: 20;
        @include edgtfTransition(width 0.2s ease-in-out, height 0.2s ease-in-out);

        &.line-1,
        &.line-3{
            height: 1px;
            width: 13px;
        }

        &.line-2,
        &.line-4{
            height: 13px;
            width: 1px;
        }

        &.line-1{
            top: 0;
            left: -6px;
        }

        &.line-2{
            left: 0;
            top: -6px;
        }

        &.line-3{
            bottom: 0;
            right: -6px;
        }

        &.line-4{
            right: 0;
            bottom: -6px;
        }
    }

    &:hover {
        .edgtf-btn-lines{
            &.line-1,
            &.line-3{
                width: 47px;
            }
            &.line-2,
            &.line-4{
                height: 47px;
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
