// widgets spacing
$widget-bottom-margin: 30px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;

// widget color
$widget-background-color: #eeeeee;

// list items dimensions
$widget-list-item-height: 1.6em;
$widget-list-item-padding: 6px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: inherit;
$widget-heading-color: $default-heading-color;

// links
$widget-link-color: $default-heading-color;
$widget-link-hover-color: rgba($default-heading-color, .7);

// specific widgets
$widget-search-form-height: 42px;
$widget-tag-cloud-height: 30px;

aside.edgtf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }

    .edgtf-single-social-link-widget{
        a{
            display: block;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .edgtf-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.edgtf-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default-wordpress-widgets-flat';
}